s new Item will be duplicated from\n                <span class=\"duplicate-text\">{{ props.duplicateFrom.name }}.workflow </span>\n            </span>\n        </div>\n        <div class=\"input-label\">Name</div>\n        <IDEInput :isRequired=\"true\" elementId=\"nameInput\" :disable-required-icon=\"true\" @getValue=\"setNameValue\"\n            @validateStatus=\"setNameValidationStatus\" placeholder=\"\" :type=\"'text'\" info-text=\"\"\n            :validations=\"validations.name\" />\n    </div>\n    <CreateDialogFooter :cancel-button-cb=\"() => emit('close')\" :create-button-cb=\"create\"\n        :create-button-enabled=\"isNameValid\" />\n</template>\n\n<style scoped>\n\n.duplicate-note-div {\n    margin-bottom: 10px;\n}\n\n.duplicate-text {\n    font-weight: bold;\n}\n</style>\n"],"sourceRoot":""}]);
